.wrapper {
 width: 100%;
 height: calc(100% - 40px); 
 position: relative;
}


.wrapper button {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  background: none;
  border: 2px solid #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-content: center;
  height: 80px;
  width: 80px;
  cursor: pointer;
}

.wrapper video {
  position: absolute;
  z-index: -1;
  opacity: 0.2;
}
.wrapper canvas {
  position: absolute;
  z-index: 1;
}